import React, { useEffect, useState } from 'react';
import {
    AppBar,
    Grid,
    Tab,
    Tabs,
    Toolbar,
    Typography,
} from '@material-ui/core';

import './App.css';
import { useDispatch, useSelector } from 'react-redux';
import Notifier from './components/Notifier';
import V1Graph from './components/V1Graph';
import V2Graph from './components/V2Graph';
import V3Graph from './components/V3Graph';
import Arbitary from './Arbitary';
import DijAutIns from './DijAutIns';
import DijkstrasAutomated from './DijkstrasAutomated';
import DijstrasTutorial from './DijstrasTutorial';
import DijTutIns from './DijTutIns';
import { actions as uiActions } from './store/slices/ui';
import { actions as v1GraphActions } from './store/slices/v1Graph';
import { actions as v2GraphActions } from './store/slices/v2Graph';
import { actions as v3GraphActions } from './store/slices/v3Graph';

const App = () => {
    const activeGraphId = useSelector((state) => state.ui.activeGraphId);
    const dispatch = useDispatch();

    const handleTabClick = (event, graphId) => {
        dispatch(uiActions.setActiveGraph({ graphId }));
    };

    useEffect(() => {
        dispatch(v1GraphActions.reset());
        dispatch(v2GraphActions.reset());
        dispatch(v3GraphActions.reset());
    }, [activeGraphId]);

    return (
        <div className="root">
            <Notifier />
            <AppBar className="appbar" position="relative">
                <Toolbar>
                    <Grid container justify="space-between" alignItems="center">
                        <Grid item>
                            <Typography variant="h6">
                                Shortest Path using Dynamic Programming
                            </Typography>
                        </Grid>
                        <Grid item>
                            <Tabs
                                value={activeGraphId}
                                onChange={handleTabClick}
                            >
                                <Tab label="Arbitary" value={1} />
                                <Tab label="Using Topological Sort" value={2} />
                                <Tab label="Using Topological (V2)" value={3} />
                                <Tab
                                    label="Using Topological (Automated)"
                                    value={4}
                                />
                                <Tab label="Dijkstras Algorithm" value={5} />
                                <Tab label="Dijkstras (Automated)" value={6} />
                            </Tabs>
                        </Grid>
                    </Grid>
                </Toolbar>
            </AppBar>
            {activeGraphId === 1 && <Arbitary />}
            {activeGraphId === 2 && <V1Graph />}
            {activeGraphId === 3 && <V2Graph />}
            {activeGraphId === 4 && <V3Graph />}

            {(activeGraphId === 5 || activeGraphId === 6) && (
                <div className="content">
                    {activeGraphId === 5 && <DijTutIns />}
                    {activeGraphId === 6 && <DijAutIns />}
                    <div className="right-div">
                        {activeGraphId === 5 && <DijstrasTutorial />}
                        {activeGraphId === 6 && <DijkstrasAutomated />}
                    </div>
                </div>
            )}
            <div className="copyright">
                <h3>
                    Copyright  2022, algodynamics.io | Development partly
                    supported by{' '}
                    <a href="https://www.cemca.org" target="_blank">
                        {' '}
                        CEMCA{' '}
                    </a>
                </h3>
            </div>
        </div>
    );
};

export default App;
